import React from 'react';
import { List, ListContent } from 'semantic-ui-react'

export const EducationList = list => list.map((item, index) => (
    <List.Item key={index}>
        <ListContent>
            <List.Header><em>{item.header}</em></List.Header>
            <List.Description>{item.description}</List.Description>
        </ListContent>
    </List.Item>
))
