import React from "react";
import { List, Header } from "semantic-ui-react";
import { HardSkillsList } from "./HardSkillsList";

export const HardSkills = props => (
  <>
    <section>
      <Header as='h3'>HARD SKILLS</Header>
      <List bulleted horizontal>{HardSkillsList(props.data)}</List>
    </section>
  </>
)
