import React from "react";
import { List, Header } from "semantic-ui-react";
import { SoftSkillsList } from './SoftSkillsList'

export const SoftSkills = props => (
  <>
    <section>
      <Header as='h3'>SOFT SKILLS</Header>
      <List bulleted horizontal>{SoftSkillsList(props.data)}</List>
    </section>
  </>
)
