import React from 'react';
import { List, ListContent } from 'semantic-ui-react'

export const ExperienceList = list => list.map((item, index) => (
    <List.Item key={index}>
        <ListContent>
            <List.Header><em>{item.title} | {item.dateStart} - {item.dateFinish}</em></List.Header>
            <List.Description>{item.desc}</List.Description>
        </ListContent>
    </List.Item>
))
